.about_container{
    display: grid;
    grid-template-columns: 35% 50%;
    gap: 15%;
    width: 100%;
}
.about_me-image {
    flex: 1;
    position: relative;
  }
  .about_me-image > * {
    position: absolute;
    z-index: 1;
  }
  .about_me-image > :nth-child(3) {
    transform: scale(0.8);
    left: 5%;
    top: -120px
  } 
.about_cards{
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 1.5rem;
}
.about_card{
  background: var(--color-bg-variant);
  border: 1px solid transparent;
  border-radius: 1rem;
  padding: 2rem;
  text-align: center;
  transition: var(--transition);
}
.about_card:hover{
  background: transparent;
  border-color: var(--color-primary-variant);
  cursor: default;
}
.about_icon{
  color: var(--color-primary);
  font-size: 1.4rem;
  margin-bottom: 1rem;
}
.about_card h5{
  font-size: 0.95rem;
}
.about_card small{
  font-size: 0.7rem;
  color: var(--color-light);
}
.about_content p{
  margin: 2rem 0 2.6rem;
  color: var(--color-light);
}
@media screen and (max-width: 1024px) {
  .about_container{
     grid-template-columns: 1fr;
     gap: 0;
  }
  .about_me-image{
    padding-bottom: 200px;
  }
  .about_me{
    width: 50%;
    margin: 2rem auto 4rem;
  }
  .about_content p{
    margin: 1rem 0 1.5rem;
  }
}
@media screen and (max-width: 600px) {
  .about_container{
    grid-template-columns: 1fr;
  }
  .about_me-image > :nth-child(3){
    top: -80px;
  }
  .about_me{
    width: 65%;
    margin: 2rem auto 3rem;
  }
  .about_cards{
    grid-template-columns: 1fr 1fr;
    gap: 1rem;
  }
  .about_content{
    text-align: center;
  }
  .about_content p{
    margin: 1.5rem 0;
  }
}